import React from 'react';
import styles from './Section5.module.css';

import posLogin from '../../assets/images/pos.png';
import posSignup from '../../assets/images/pos.png';
import posMain from '../../assets/images/pos.png';
import posQuickButton from '../../assets/images/pos.png';
import posSettings from '../../assets/images/pos.png';
import posTableEdit from '../../assets/images/pos.png';
import posMenuEdit1 from '../../assets/images/pos.png'; //    

const Section5 = () => {
  return (
    <div className={styles.posSection5}>
      <h1>POS </h1>
      <div className={styles.posImageContainer}>
        <div className={styles.posImageBox}>
          <img src={posLogin} alt="POS " />
          <p>POS </p>
        </div>
        <div className={styles.posImageBox}>
          <img src={posSignup} alt="POS " />
          <p>POS </p>
        </div>
        <div className={styles.posImageBox}>
          <img src={posMain} alt="POS " />
          <p>POS </p>
        </div>
        <div className={styles.posImageBox}>
          <img src={posQuickButton} alt="POS " />
          <p>POS </p>
        </div>
        <div className={styles.posImageBox}>
          <img src={posSettings} alt="POS " />
          <p>POS </p>
        </div>
        <div className={styles.posImageBox}>
          <img src={posTableEdit} alt="POS " />
          <p>POS </p>
        </div>
        <div className={styles.posImageBox}>
          <img src={posMenuEdit1} alt="POS " />
          <p>POS </p>
        </div>
      </div>
      <div className={styles.posVideoContainer}>
        {/*    */}
        <iframe
          width="100%"
          height="315"
          src="https://www.youtube.com/embed/stwp4vslnmc"
          title="YouTube video player"
          frameBorder="0"
          allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
          allowFullScreen
        ></iframe>
      </div>
    </div>
  );
};

export default Section5;
