import React, { useEffect, useRef } from "react";
import { Network } from "vis-network";
import { DataSet } from "vis-data";
import styles from "./Section4.module.css";

const Section4 = () => {
  const diagramContainer = useRef(null);

  useEffect(() => {
    const container = diagramContainer.current;
    let network = null;
    let animationFrameId = null;
    let animate = null; //   'animate'  

    //   
    const nodes = new DataSet([
      {
        id: 1,
        label: "",
        shape: "box",
        color: "#FFC0CB", // 
        x: -400,
        y: -300,
        font: {
          size: 30,
          face: "sans-serif",
          color: "#333333", //   
          mod: "bold", //  
        },
        widthConstraint: { minimum: 150 },
        heightConstraint: { minimum: 100 },
        shapeProperties: {
          borderRadius: 20,
        },
      },
      {
        id: 3,
        label: "POS",
        shape: "box",
        color: "#D8BFD8", // 
        x: 0,
        y: -300,
        font: {
          size: 30,
          face: "sans-serif",
          color: "#333333",
          mod: "bold",
        },
        widthConstraint: { minimum: 150 },
        heightConstraint: { minimum: 100 },
        shapeProperties: {
          borderRadius: 20,
        },
      },
      {
        id: 2,
        label: "Firebase",
        shape: "box",
        color: "#98FB98", // 
        x: 0,
        y: 0,
        font: {
          size: 45,
          face: "sans-serif",
          color: "#333333",
          mod: "bold",
        },
        widthConstraint: { minimum: 250 },
        heightConstraint: { minimum: 150 },
        shapeProperties: {
          borderRadius: 20,
        },
      },
      {
        id: 7,
        label: "KIOSK",
        shape: "box",
        color: "#D8BFD8", // 
        x: 0,
        y: 300,
        font: {
          size: 30,
          face: "sans-serif",
          color: "#333333",
          mod: "bold",
        },
        widthConstraint: { minimum: 150 },
        heightConstraint: { minimum: 100 },
        shapeProperties: {
          borderRadius: 20,
        },
      },
      {
        id: 4,
        label: "NFC",
        shape: "box",
        color: "#FFC0CB", // 
        x: 400,
        y: -300,
        font: {
          size: 30,
          face: "sans-serif",
          color: "#333333",
          mod: "bold",
        },
        widthConstraint: { minimum: 150 },
        heightConstraint: { minimum: 100 },
        shapeProperties: {
          borderRadius: 20,
        },
      },
      {
        id: 8,
        label: "",
        shape: "box",
        color: "#FFC0CB", // 
        x: 400,
        y: 300,
        font: {
          size: 30,
          face: "sans-serif",
          color: "#333333",
          mod: "bold",
        },
        widthConstraint: { minimum: 150 },
        heightConstraint: { minimum: 100 },
        shapeProperties: {
          borderRadius: 20,
        },
      },
    ]);

    //   
    const edges = new DataSet([
      {
        id: 1,
        from: 1,
        to: 3,
        arrows: "to",
        label: "pos  ",
        font: { color: "#DEDEDE", size: 18, bold: true, strokeWidth: 0 },
      },
      {
        id: 2,
        from: 2,
        to: 3,
        arrows: "to, from",
        label: "pos  \n  ",
        font: { color: "#DEDEDE", size: 18, bold: true, strokeWidth: 0 },
      },
      {
        id: 3,
        from: 4,
        to: 8,
        arrows: "to",
        label: "  ",
        font: { color: "#DEDEDE", size: 18, bold: true, strokeWidth: 0 },
      },
      {
        id: 4,
        from: 8,
        to: 7,
        arrows: "to",
        label: "",
        font: { color: "#DEDEDE", size: 18, bold: true, strokeWidth: 0 },
      },
      {
        id: 5,
        from: 2,
        to: 7,
        arrows: "to, from",
        label: "  \n  ",
        font: { color: "#DEDEDE", size: 18, bold: true, strokeWidth: 0 },
      },
      {
        id: 6,
        from: 3,
        to: 4,
        arrows: "to",
        label: " URL ",
        font: { color: "#DEDEDE", size: 18, bold: true, strokeWidth: 0 },
      },
    ]);
    


    const data = {
      nodes: nodes,
      edges: edges,
    };

    const options = {
      physics: {
        enabled: false,
      },
      layout: {
        randomSeed: 42,
      },
      interaction: {
        dragNodes: true, //   
        dragView: false,
        zoomView: false,
        selectable: false,
        hover: true, //    
      },
      manipulation: {
        enabled: false,
      },
      edges: {
        width: 6,
        smooth: {
          type: "cubicBezier",
          forceDirection: "horizontal",
          roundness: 0.4,
        },
        color: {
          color: "#696969", //  
          highlight: "#696969",
          hover: "#696969",
          inherit: false,
        },
        arrows: {
          to: { enabled: true, scaleFactor: 2 },
          from: { enabled: true, scaleFactor: 2 },
        },
      },
    };

    const initializeNetwork = () => {
      network = new Network(container, data, options);

      //     
      const originalPositions = {};
      const originalStyles = {};
      nodes.forEach((node) => {
        originalPositions[node.id] = { x: node.x, y: node.y };
        originalStyles[node.id] = {
          color: node.color,
          font: { ...node.font },
          size: node.size || 25,
        };
      });

      //    
      const edgeIds = edges.getIds();
      let animationProgress = {};
      let reverseAnimationProgress = {};

      //   
      const bidirectionalEdges = [2, 5]; //   ID 

      edgeIds.forEach((id) => {
        animationProgress[id] = 0;
        if (bidirectionalEdges.includes(id)) {
          reverseAnimationProgress[id] = 1; //     
        }
      });

      //     
      const updateAnimation = () => {
        if (!network) return;

        edgeIds.forEach((id) => {
          animationProgress[id] += 0.005; //      
          if (animationProgress[id] > 1) {
            animationProgress[id] = 0;
          }

          if (bidirectionalEdges.includes(id)) {
            reverseAnimationProgress[id] -= 0.005;
            if (reverseAnimationProgress[id] < 0) {
              reverseAnimationProgress[id] = 1;
            }
          }
        });

        network.redraw();
        animationFrameId = requestAnimationFrame(updateAnimation);
      };

      // 'animate'      
      animate = (ctx) => {
        ctx.save();
        //   
        edgeIds.forEach((id) => {
          const edge = network.body.edges[id];
          if (edge) {
            const progress = animationProgress[id];
            const point1 = edge.edgeType.getPoint(progress);
            const point2 = edge.edgeType.getPoint(progress + 0.01);

            //    (   )
            drawArrow(
              ctx,
              point1.x,
              point1.y,
              point2.x,
              point2.y,
              bidirectionalEdges.includes(id) ? "#FFB6C1" : "#87CEFA", //  
              6, //  
              20 //  
            );

            //       
            if (bidirectionalEdges.includes(id)) {
              const reverseProgress = reverseAnimationProgress[id];
              const reversePoint1 = edge.edgeType.getPoint(reverseProgress);
              const reversePoint2 = edge.edgeType.getPoint(
                reverseProgress - 0.01
              );

              drawArrow(
                ctx,
                reversePoint1.x,
                reversePoint1.y,
                reversePoint2.x,
                reversePoint2.y,
                "#FFDAB9", //   
                4, //  
                15 //  
              );
            }
          }
        });
        ctx.restore();

        //   
        Object.values(network.body.nodes).forEach((node) => {
          node.draw(ctx);
        });
      };

      //   
      const drawArrow = (
        ctx,
        fromX,
        fromY,
        toX,
        toY,
        color,
        lineWidth = 2,
        headlen = 10
      ) => {
        const angle = Math.atan2(toY - fromY, toX - fromX);

        ctx.beginPath();
        ctx.moveTo(fromX, fromY);
        ctx.lineTo(toX, toY);
        ctx.strokeStyle = color;
        ctx.lineWidth = lineWidth;
        ctx.stroke();

        //  
        ctx.beginPath();
        ctx.moveTo(toX, toY);
        ctx.lineTo(
          toX - headlen * Math.cos(angle - Math.PI / 6),
          toY - headlen * Math.sin(angle - Math.PI / 6)
        );
        ctx.lineTo(
          toX - headlen * Math.cos(angle + Math.PI / 6),
          toY - headlen * Math.sin(angle + Math.PI / 6)
        );
        ctx.lineTo(toX, toY);
        ctx.lineTo(
          toX - headlen * Math.cos(angle - Math.PI / 6),
          toY - headlen * Math.sin(angle - Math.PI / 6)
        );
        ctx.fillStyle = color;
        ctx.fill();
      };

      // 'afterDrawing'  
      network.on("afterDrawing", (ctx) => {
        animate(ctx);
      });

      // 'dragEnd'   
      network.on("dragEnd", (params) => {
        params.nodes.forEach((nodeId) => {
          const originalPosition = originalPositions[nodeId];
          if (originalPosition) {
            nodes.update({
              id: nodeId,
              x: originalPosition.x,
              y: originalPosition.y,
            });
          }
        });
      });

      // 'hoverNode'   
      network.on("hoverNode", (params) => {
        const nodeId = params.node;
        const node = nodes.get(nodeId);

        //    (:  ,   )
        nodes.update({
          id: nodeId,
          font: { ...node.font, size: node.font.size + 5 },
          borderWidth: 3,
          borderWidthSelected: 3,
          color: {
            ...node.color,
            border: "#DEDEDE", 
          },
        });
      });

      // 'blurNode'   
      network.on("blurNode", (params) => {
        const nodeId = params.node;
        const originalStyle = originalStyles[nodeId];

        //    
        nodes.update({
          id: nodeId,
          font: originalStyle.font,
          borderWidth: 1,
          borderWidthSelected: 2,
          color: {
            ...originalStyle.color,
            border: undefined,
          },
        });
      });

      //  
      updateAnimation();
    };

    initializeNetwork();

    //     
    return () => {
      if (network) {
        network.off("afterDrawing", animate);
        network.off("dragEnd");
        network.off("hoverNode");
        network.off("blurNode");
        network.destroy();
      }
      if (animationFrameId) {
        cancelAnimationFrame(animationFrameId);
      }
    };
  }, []);

  return (
    <div className={styles.section4}>
      <div ref={diagramContainer} className={styles.diagramContainer}></div>
    </div>
  );
};

export default Section4;
