import React from 'react';
import styles from './Section3One.module.css';
import FigmaLogo from '../../assets/logo/figma.svg'; // Figma  import
import JSLogo from '../../assets/logo/JS.png'; // JavaScript PNG  import
import ReactLogo from '../../assets/logo/react.png'; // React  import
import NodeLogo from '../../assets/logo/Node.js.png'; // Node.js PNG  import
import FirebaseLogo from '../../assets/logo/firebase.png'; // Firebase PNG  import
import VSCodeLogo from '../../assets/logo/vscode.png'; // VSCode PNG  import
import OpenAILogo from '../../assets/logo/openai.png'; // OpenAI PNG  import
import NotionLogo from '../../assets/logo/notion.png'; // Notion PNG  import
import CursorLogo from '../../assets/logo/cursor.png'; // Cursor PNG  import

const Section3One = () => {
  return (
    <div className={styles.section3One}>
      <div className={styles.projectNameContainer}>
        <h1>OrderIn</h1> {/*     */}
      </div>
      <div className={styles.developmentPeriodContainer}>
        <p> 2024-08-01</p> {/*    */}
      </div>
      <div className={styles.developmentStackContainer}>
        <div className={styles.stackBox}>
          design
          <img src={FigmaLogo} alt="Figma " className={styles.logo} />
        </div>
        <div className={`${styles.stackBox} ${styles.frontendStackBox}`}>
          frontend
          <div className={styles.logoRow}>
            <img src={JSLogo} alt="JavaScript " className={styles.logo} />
            <img src={ReactLogo} alt="React " className={styles.logo} />
          </div>
        </div>
        <div className={`${styles.stackBox} ${styles.backendStackBox}`}>
          backend
          <div className={styles.logoRow}>
            <img src={NodeLogo} alt="Node.js " className={styles.logo} />
            <img src={FirebaseLogo} alt="Firebase " className={styles.logo} />
          </div>
        </div>
        <div className={`${styles.stackBox} ${styles.toolStackBox}`}>
          development tool
          <div className={styles.logoGrid}>
            <img src={VSCodeLogo} alt="VSCode " className={styles.logo} />
            <img src={OpenAILogo} alt="OpenAI " className={styles.logo} />
            <img src={NotionLogo} alt="Notion " className={styles.logo} />
            <img src={CursorLogo} alt="Cursor " className={styles.logo} />
          </div>
        </div>
      </div>
    </div>
  );
};

export default Section3One;
